import logo from './logo.svg';
import './App.css';
import React, { createRef } from 'react';

import bird from './img/bird.png'
import tubeUp from './img/tubeUp.jpg'
import tubeBottom from './img/tubeDown.jpg'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gameStart: false,
      isJumping: false,
      gameScore: 0,
      isPassed: false,
    };

    this.birdBox = React.createRef();
    this.gameContainer = React.createRef();

    this.tubeTop1 = React.createRef();
    this.tubeBottom1 = React.createRef();

    this.keyDownHandler = this.keyDownHandler.bind(this);
    this.startHandler = this.startHandler.bind(this);
    this.birdPosition = this.birdPosition.bind(this);
    this.collisionDetection = this.collisionDetection.bind(this);
    this.gameTitle = this.gameTitle.bind(this);
    this.scoreTrigger = this.scoreTrigger.bind(this);

    this.birdMove = this.birdMove.bind(this);
    this.tubesTrigger = this.tubesTrigger.bind(this);
    this.endScreen = this.endScreen.bind(this);
  }

  intervalID = 0;
  intervalID1 = 1;

  componentDidMount() {
    window.addEventListener('keydown', this.startHandler);
    window.addEventListener('keydown', this.keyDownHandler);
    this.birdPosition();
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.startHandler);
    window.removeEventListener('keydown', this.keyDownHandler);
  }

  startHandler(eventKey) {
    switch (eventKey.key) {
      case " ":
        if (this.state.gameStart === false) {
          this.birdBox.current.className = "character";
          this.setState({ gameStart: true, isJumping: false, gameScore: 0 }, () => {
            this.birdMove();
            this.gameContainer.current.className = "gameBackground-start";
            this.gameContainer.current.style.animationPlayState = "initial";
            this.collisionDetection();
            window.removeEventListener('keydown', this.startHandler);
            this.tubesTrigger();
            this.scoreTrigger();
          })
        }
    }
  }

  keyDownHandler(eventKey) {
    switch (eventKey.key) {
      case " ":
        if (this.state.gameStart === true) {
          this.birdMove();
        }
        if (this.state.gameStart === false) {
          this.birdBox.current.className = "character-reset";
          this.birdPosition();
          this.setState({ isJumping: false });
          this.gameContainer.current.className = "gameBackground";
          window.addEventListener('keydown', this.startHandler);
        }
        break;
    }
  }

  birdMove() {
    if (this.state.isJumping === false) {
      let birdTop = this.birdBox.current.getBoundingClientRect().top;
      this.birdBox.current.className = "character"
      this.birdBox.current.style.top = birdTop - 50 + "px";

      this.setState({ isJumping: true }, () => {
        setTimeout(() => {
          this.setState({ isJumping: false });
          this.birdBox.current.className = "character-drop"
          this.birdBox.current.style.top = 588 + "px";
        }, 210);
      })
    } else {
      //pass
    }
  }

  birdPosition() {
    let gameTop = this.gameContainer.current.getBoundingClientRect().top;
    let gameBottom = this.gameContainer.current.getBoundingClientRect().bottom;
    let setBirdTop = (gameTop + gameBottom) / 2;
    let gameLeft = this.gameContainer.current.getBoundingClientRect().left;
    let gameRight = this.gameContainer.current.getBoundingClientRect().right;
    let setBirdLeft = (gameLeft + gameRight) / 2;

    this.birdBox.current.style.top = setBirdTop - 18 + "px";
    this.birdBox.current.style.left = setBirdLeft - 26 + "px";
  }

  collisionDetection() {
    const collisionDetectionInterval = setInterval(() => {

      let rect1 = this.birdBox.current.getBoundingClientRect();
      let rect2 = this.tubeTop1.current.getBoundingClientRect();
      let rect3 = this.tubeBottom1.current.getBoundingClientRect();

      if (rect1.left < rect2.left + rect2.width &&
        rect1.left + rect1.width > rect2.left &&
        rect1.top < rect2.top + rect2.height &&
        rect1.height + rect1.top > rect2.top ||
        rect1.left < rect3.left + rect3.width &&
        rect1.left + rect1.width > rect3.left &&
        rect1.top < rect3.top + rect3.height &&
        rect1.height + rect1.top > rect3.top
      ) {
        this.gameContainer.current.style.animationPlayState = "paused";
        this.tubeTop1.current.className = "tubeStay";
        this.tubeBottom1.current.className = "tubeStay";
        this.birdBox.current.className = "character";
        this.setState({ isJumping: true, gameStart: false }, () => {
          clearInterval(this.intervalID);
          clearInterval(this.intervalID1);
        });
        clearInterval(collisionDetectionInterval);
      }
    }, 10);
  }

  gameTitle() {
    if (this.state.gameStart === false && this.state.isJumping === true) {
      return (
        <div className="gameTitle">Click Space To Reset</div>
      )
    } else if (this.state.gameStart === false && this.state.isJumping === false) {
      return (
        <div className="gameTitle">Click Space To Start</div>
      )
    } else {
      return (
        <div className="gameTitle">{this.state.gameScore}</div>
      )
    }
  }

  tubesTrigger() {
    this.intervalID = setInterval(() => {
      let randomInt = Math.floor(Math.random() * 212) + 50
      this.tubeTop1.current.style.top = "-" + randomInt + "px";
      this.tubeBottom1.current.style.top = 512 - randomInt - 50 + "px";
      this.tubeTop1.current.className = "tubeMove";
      this.tubeBottom1.current.className = "tubeMove";
      this.tubeTop1.current.style.animationPlayState = "initial";
      this.tubeBottom1.current.style.animationPlayState = "initial";
      this.setState({ isPassed: false })
    }, 3000);
  }

  scoreTrigger() {
    this.intervalID1 = setInterval(() => {
      if (this.state.isPassed === false) {
        let rect1 = this.birdBox.current.getBoundingClientRect();
        let rect2 = this.tubeTop1.current.getBoundingClientRect();
        if(rect1.left > rect2.left + rect2.width){
          let gameScoreThen = this.state.gameScore + 1;
          this.setState({ gameScore: gameScoreThen, isPassed: true})
        }
      }
    }, 50)
  }

  endScreen() {
    if(this.state.gameStart === false && this.state.gameScore > 0){
      return(
        <div className="endScreenText">You Score Is {this.state.gameScore}</div>
      )
    } else {
      return null
    }
  }

  render() {
    return (
      <div className="gameHolder">
        {/* game background width */}
        <div className="gameBackground" ref={this.gameContainer}>
          <this.gameTitle />
          <this.endScreen />
          {/* group of tubes */}

          <div className="tubeStay" ref={this.tubeTop1}><img src={tubeBottom} /></div>
          <div className="tubeStay" ref={this.tubeBottom1}><img src={tubeUp} /></div>


          <div className="character" ref={this.birdBox}><img className="birdImg" src={bird} /></div>
        </div>
      </div>
    );
  }
}

export default App;